import { ErrorIcon } from "../ErrorIcon";
import { SuccessIcon } from "../SuccessIcon";
import { CloseIcon } from "../CloseIcon";
import { motion } from "framer-motion"
import "../../styles/tailwindcss"
import React from "react";

export const MyPopup = ({ text, type, title, trigger, onClose }) => {
  let successColor = "[#1BA11B]";
  let errorColor = "[#CC2727]";

  const displayAnimations = {
    open: {
      opacity: 1,
      x: "-50%",
    },
    close: {
      opacity: 0,
      x: "-100%",
    },
  };

  return (
    <motion.div
      className={
        "absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-[400px] shadow-[0px_0px_6px_rgba(0,0,0,0.15)] rounded overflow-hidden "
      }
      animate={trigger ? "open" : "closed"}
      variants={displayAnimations}
      initial="close"
    >
      <div
        className={
          "w-full h-full border-l-4 flex px-5 py-3 items-center gap-4 " +
          (type === "success"
            ? "border-[#1BA11B]"
            : type === "error"
            ? "border-[#CC2727]"
            : "")
        }
      >
        {type === "success" ? (
          <SuccessIcon stroke="#1BA11B" width="25px" overflow="visible" />
        ) : type === "error" ? (
          <ErrorIcon stroke="#CC2727" width="25px" overflow="visible" />
        ) : (
          ""
        )}
        <div className="basis-full">
          <p
            className={
              "font-bold uppercase " +
              (type === "success"
                ? "text-[#1BA11B]"
                : type === "error"
                ? "text-[#CC2727]"
                : "")
            }
          >
            {title}
          </p>
          {text}
        </div>
        <CloseIcon
          className=" cursor-pointer ml-2 hover:scale-150 transition-all"
          onClick={onClose}
        />
      </div>
    </motion.div>
  );
};
